html{height:100%}body{height:100%;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#f8f8f8 !important}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

div.header .header-title{width:100%;background-color:#002339}div.header .header-title .container{text-align:left;margin:0px auto;padding:30px 10px}div.header .header-title .container h1{margin:0px;font-weight:bold;color:#ffffff}div.header .header-nav{padding:0 10px}div.header .header-nav .container{padding:10px;margin:15px auto;border:1px solid #007bff;border-radius:5px}

.footer{font-size:12px;height:30px;position:relative;margin-top:80px}

*{box-sizing:border-box;font-family:"Helvetica", sans-serif}.container{text-align:center;padding:15px;margin:10px auto}.react-csv input{padding:10px;display:block;margin:0 auto;border:1px solid #ccc;border-radius:5px}.container p{padding-top:10px;color:#666}

.Calculator{margin-top:-45px}.Calculator .row{padding:15px 0}.Calculator .row>div{margin:10px auto}@media only screen and (min-width: 1025px){.Calculator .row .row-title{text-align:center}}@media only screen and (max-width: 1024px){.Calculator .row .row-title{text-align:center}}.Calculator .row .row-title h2{font-weight:bold}.Calculator .calculator-textarea textarea{margin:0 auto;height:100%;width:90%;font-size:large}.Calculator .calculator-textarea .badge{margin-top:20px;font-size:16px;font-weight:lighter}.Calculator .calculator-csv-reader .badge a{color:#fff;text-decoration:none}.Calculator .calculator-csv-reader .badge .glyphicon{margin-right:6px;font-size:14px}.Calculator .calculator-score .score-type{position:relative;text-align:center;height:50px}.Calculator .calculator-score .score-type ::before{content:'';height:100%;display:inline-block;vertical-align:middle}.Calculator .calculator-score .score-type>span{display:inline-block;vertical-align:middle}.Calculator .calculator-score .score-type .badge{font-size:14px}.Calculator .calculator-score .score-type .score-value{margin-left:25px;font-size:32px}.Calculator .calculator-score .rmse{padding:0px;color:#dc3545}.Calculator .calculator-score .mape{padding:0px;color:#007bff}.Calculator .calculator-score form .form-row{margin:0 5px}.Calculator .calculator-score form .form-row .form-group{margin:0 auto}.Calculator .calculator-score form .form-row .form-group .btn{border-radius:.25rem;border-top-left-radius:0;border-bottom-left-radius:0}

